.right {
    display: flex;
    height: 100vh;
    width: calc(100% - 300px);
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;

    .playerContainer {
        width: 100%;
        display: none;

        .play {
            width: 50px;
            height: 50px;
            border-radius: 200px;
            border: 2px solid white;
            margin: 0 auto;
        }

        .time {
            width: 100px;
            background: white;
            border-radius: 3px;
            margin: 0 auto;
            margin-top: 10px;
            text-align: center;
            padding: 5px;
            color: #555;
            font-size: 0.75em;
        }

        .line {
            max-width: 90%;
            margin-left: 5%;
            height: 50px;
            position: relative;

            .total {
                height: 1px;
                background: #999;
                top: 50%;
                transform: translate(0, -50%);
                position: absolute;
                width: 100%;
            }

            .current {
                height: 5px;
                width: 0%;
                background: white;
                top: 50%;
                transform: translate(0, -50%);
                position: absolute;
            }
        }

        .subsContainer {
            position: absolute;
            width: 100%;
            height: 100%;

            div {
                position: absolute;
                width: 1px;
                height: 50%;
                top: 0;
                transform: translate(-50%, 0);
                background: #999;
                cursor: pointer;

                span {
                    position: absolute;
                    width: 10px;
                    height: 10px;
                    top: -10px;
                    left: 50%;
                    transform: translate(-50%, 0);
                    background: white;
                    border-radius: 20px;
                }
            }
        }

        .notice {
            width: calc(90% - 20px);
            margin-left: 5%;
            margin-bottom: 50px;
            text-align: center;
            padding: 10px;
            border-radius: 3px;
            color: white;

            a {
                color: #999;

                &:hover {
                    color: black;
                }
            }
        }

        &.active {
            display: block;
        }
    }
}