.left {
    color: white;
    display: flex;
    width: 300px;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    border-left: 1px solid #999;

    .top {
        padding: 10px;

        img {
            max-width: 80px;
            display: block;
            margin: 0 auto;
            margin-bottom: 10px;
        }
    }

    .item {
        width: 100%;
        padding: 10px;
        border-bottom: 1px solid #999;

        &:nth-child(2) {
            border-top: 1px solid #999;
        }

        .des {
            font-size: 1em;
            margin-top: 10px;
            color: #999;
        }

        .top {
            display: flex;
            align-items: center;

            .name {
                font-size: 0.9em;
                margin-left: 10px;
                width: calc(100% - 40px);
            }
            .icon {
                width: 40px;
                height: 40px;
                border-radius: 50px;
                border: 1px solid white;
                cursor: pointer;
                transition: all 0.25s ease;
                position: relative;

                &:hover {
                    background: #444;
                }

                &::before {
                    font-family: 'icons';
                    line-height: 40px;
                    text-align: center;
                    width: 100%;
                    position: absolute;
                    content: '\E800';
                    font-size: 0.7em;
                }
            }
        }

        &.active {

            .top {
                .icon {
                    &::before {
                        content: '\e801';
                    }
                }
            }
            
        }
        
    }
}